











































import Vue from "vue";
const googleLoginURL: string = `${process.env.VUE_APP_API_URL}/auth/google`;
const loginURL: string = `${process.env.VUE_APP_API_URL}/auth/login`;
const authCheckURL: string = `${process.env.VUE_APP_API_URL}`;
export default Vue.extend({
  name: "login",
  data: () => ({
    email: "",
    password: ""
  }),
  mounted() {
    fetch(authCheckURL, { method: "GET" , credentials:'include'})
      .then(response => response.json())
      .then(result => {
        if(result && result.authenticated){
          this.$router.push('/');
        }
      });
  },
  methods: {
    loginGoogle() {
      window.location.href = googleLoginURL;
    },
    login() {
      const user = {
        email: this.email,
        password: this.password
      };
      fetch(loginURL, {
        method: "POST",
        credentials: "include",
        body: JSON.stringify(user),
        headers: { "content-type": "application/json" }
      })
        .then(response => {
          if (response.ok) {
            return response.json();
          }
          return response.json().then(error => {
            throw new Error(error.message);
          });
        })
        .then(user => {
          localStorage.user = user;
          this.$router.push("/");
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
});
